header{
    padding: 10px 15px;
}
header div{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
header img{
    width: 200px;
}
header a{
    font-size: 18px;
    color: #000;
    text-decoration: none;
    padding: 5px;
    margin: 0 5px;
}
header a:hover{
    opacity: 85%;
}
@media (max-width: 550px){
    .topo{
        display: flex;
        flex-direction: column;
    }
    .links{
        width: 100%;
        text-align: center;
        padding: 60px 10px;
        margin-top: 10px;
        border-top: 1px solid #aaadaf;
    }
   

}