.secao-sobre{
    background-color: #f6f6f6;
    padding: 100px 20px;
    text-align: center;
}
.container-sobre p{
    line-height: 32px;
}
.box-cards{
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
}
.box-cards img,
.card{
    width: 50%;
    max-height: 300px;
    border: 1px solid #a2adaa;
}
.box-cards{
    object-fit: cover;
}
.card{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #fff;
    padding: 20px 25px;
}
.card h4{
    font-size: 36px;
    text-transform: uppercase;
    font-family: 'Anybody', cursive;
    letter-spacing: 5px;
    line-height: 42px;
}
.card p{
    font-size: 22px;
    color: #4a4a4a;
}
@media (max-width: 768px){
    .secao-sobre{
        padding: 60px 20px;
    }
    .card{
        padding: 15px;
    }
    .card h4{
        font-size: 22px;
    }
    .card p{
        font-size: 16px;
    }
}
@media (max-width: 550px){
    .secao-sobre{
        padding: 60px 10px;
    }
    .box-cards{
        flex-direction: column;
    }
    .box-cards img, .card{
        width: 100%;
        height: 300px;
    }
}